.users {
    padding: 20px;
}
.user-item {
    color: rebeccapurple;
    display: flex;
    margin-bottom: 15px;
}
.user-item__side {
    margin-right: 20px;
}
.user-item__img {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    margin-bottom: 10px;
    display: block;
}
.user-item__main{
    border: 1px solid black;
    padding: 15px;
    border-radius: 15px;
}
.pagination__button._active {
    font-weight: 700;
    color: brown;
}